
*{
    padding: 0;
    margin: 0;
    
}

li{
    list-style-type: none;
}
.nav{
  height: 60px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0px 0px 5px #ccc;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.imgDiv{
  width: 100%;
  height: 100%;
  overflow-wrap: break-word;
}
img{
  width: 40px;
  height: 40px;
  border: none;
  margin: 10px 0px;
  
}

.UnionTitle{
  width: 50%;
  height: 40px;
  margin: 10px auto;
  float: right;
}

.xsMoreBtn{
  height: 40px;
  margin: 10px 0px;
}

.search{
  height: 30px;
  width: 100%;
  margin: 15px 0;
}

.nav-text{
    color: black;
  }
  
  .leftNav{
    position: fixed;
    height: 100%;
    border-top: none;
  z-index: 9;

